#error-symbol {
    font-size: 50px;
    border: 1px solid;
    border-radius: 50%;
    height: 55px;
    width: 55px;
    margin: auto;
}

.internet-error {
  height: 60px;
  margin-top: 0;
  font-size: 20px;
}

.internet-error p {
  font-family: 'Quicksand';
  font-size: 25px;
  line-height: 60px;
  color: black;
  margin: 0;
}

/*loading icon */
#lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  padding-top: 20px;
 
}
#lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #fff;
  border-radius: 50%;
  -webkit-animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
          animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: black transparent transparent transparent;
}
#lds-ring div:nth-child(1) {
  -webkit-animation-delay: -0.45s;
          animation-delay: -0.45s;
}
#lds-ring div:nth-child(2) {
  -webkit-animation-delay: -0.3s;
          animation-delay: -0.3s;
}
#lds-ring div:nth-child(3) {
  -webkit-animation-delay: -0.15s;
          animation-delay: -0.15s;
}
@-webkit-keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  background-color: #E6B3B3;
  transition: background-color 2s;
  height: 100vh;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  margin-top: 20px;
  padding: 20px 30px;
}

a {
  text-decoration: none;
  color: white;
}

#quote-box {
  display: visible;
  width: 600px;
  height: 250px;
  color:#E6B3B3;
  transition: color 2s;
  background-color: white;
  text-align: center;
  padding: 0px 0px 50px 0px;
  border-radius: 5px;
}

#text {
  background-color: white;
  font-family: 'Amatic';
  font-size: 1.6em;
  margin: 30px 20px;
  padding-top:20px;
}

#author {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  background-color: white;
  padding-right: 40px;
  font-family: 'Amatic';
  font-size:15px;
}

#tweet-btn{
  position: relative;
  margin-right: 60%;
}

#creator {
  display: flex;
  justify-content:center;
  font-family: 'Quicksand';
  color: white;
}

button {
  background-color: #E6B3B3;
  border: none;
  border-radius: 5px;
  width: 60px;
  height: 50px;
  font-size: 10px;
  padding: 5px 5px;
  margin-right: 10px;
  color: white;
  
  opacity: 1;
  -webkit-transform: translate(0px, 3px);
          transform: translate(0px, 3px);
  transition: background-color 2s, -webkit-transform 0.5s ease-in;
  transition: background-color 2s, transform 0.5s ease-in;
  transition: background-color 2s, transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
}

button:hover{
  opacity: .8;
  -webkit-transform: translate(0px, -3px);
          transform: translate(0px, -3px);
  transition: background-color 2s,-webkit-transform .5s ease-out;
  transition: background-color 2s,transform .5s ease-out;
  transition: background-color 2s,transform .5s ease-out,-webkit-transform .5s ease-out;
}


@media screen and (width: 790px) {
  button{
    font-size: 20px;
  }
}






  
